<template>
	<div class="msg-content-wrapper mt-3">
		<div v-if="message.length == 0">
			<p class="text-center">
				<i class="fa fa-spinner fa-spin"></i>
			</p>
		</div>
		<div v-if="message.length != 0" id="msg-content" class="msg-content">
			<div v-for="(msg_day) in message" :key="msg_day.id">
				<p class="text-center mb-1 mt-3">{{msg_day.month + ' ' + msg_day.day}}{{(msg_day.year == cur_year) ? '' : ', '+msg_day.year}}</p>
				<div v-for="(msg) in msg_day.data" class="msg-bubble-wrap" :key="msg.id">
					<div class="msg-bubble" :class="{ 'admin':msg.is_admin }">
						<p class="mb-1"><small>{{msg.name}}</small></p>
						<p class="mb-0">{{msg.message}}</p>
						<p class="text-right mb-0"><small>{{msg.time.hour+':'+msg.time.minute}}</small></p>
					</div>
				</div>
			</div>
		</div>
		<hr style="background: white; border-width: 2px" />
		<div class="msg-control">
			<div class="row">
				<div class="col-9">
					<textarea class="form-control" placeholder="Message" v-model="form.message" maxlength="100"></textarea>
					<p class="text-right mb-0"><small>{{100 - form.message.length}}</small></p>
				</div>
				<div class="col-3">
					<button v-b-modal.modalProfil class="btn btn-sm btn-block btn-outline">{{form.name}}</button>
					<button class="btn btn-sm btn-block" @click="sendMessage" :disabled="disabled_send">
						Send {{(send_countdown == 0 || !disabled_send) ? '' : secondToMinute(send_countdown)}}
					</button>
				</div>
			</div>
		</div>
		<b-modal id="modalProfil" title="Profil" hide-footer>
			<label>Name</label>
			<input type="text" class="form-control jon-form" v-model="form.name" maxlength="15">
			<p class="text-right mb-0"><small>{{15 - form.name.length}}</small></p>
			<p class="text-center mt-3 mb-0">
				<a class="btn btn-sm btn-black" @click="setName">OK</a>
			</p>
		</b-modal>
		<b-modal id="modalAdmin" hide-footer hide-header>
			<input type="text" class="form-control jon-form" v-model="form.secret">
			<p class="text-center mt-3 mb-0">
				<a class="btn btn-sm btn-black" @click="setSecret">OK</a>
			</p>
		</b-modal>
	</div>
</template>

<script>
import firebase from './../services/firebase'
import parsingDate from './../mixins/parsingDate'
import msg_data from '../data/message.json'
export default {
	data(){
		return {
			message: [],
			form: {
				name: 'No Name',
				message: '',
				secret: '',
				is_admin: 0
			},
			cur_year: 2021,
			disabled_send: false,
			send_countdown: 11,
			init_send_countdown: 11
		}
	},
	mixins: [parsingDate],
	methods:{
		initialize(){
			var cur_date = new Date()
			this.cur_year = cur_date.getFullYear()
			this.form.message = ''

			if(navigator.onLine){
				// console.log('online')
				// from firestore
				let msg_data = []
				const db = firebase.firestore()
				db.collection('message').orderBy("created_at", "asc")
				.get()
				.then((q) => {
					q.forEach((doc)=>{
						msg_data.push({
							id: doc.id,
							name: doc.data().name,
							message: doc.data().message,
							created_at: doc.data().created_at,
							is_admin: doc.data().is_admin,
							is_deleted: doc.data().is_deleted
						})
					})
					this.messageDataFormatting(msg_data)
				})
				.catch((error) => {
					console.error("Error getting documents: ", error)
				});
			} else {
				this.messageDataFormatting(msg_data)
			}

			if(localStorage.user){
				var user = JSON.parse(localStorage.getItem('user'))
				this.form.name = user.name
				this.form.secret = user.secret
			}
		},
		messageDataFormatting(msg_data){
			// console.log(msg_data)
			let msg_per_day = []
			let arr_day = []
			var daymsg = ''
			var monthmsg = ''
			var yearmsg = ''
			msg_data.forEach((e) => {
				e.time = this.parseDate(e.created_at)
				daymsg = e.time.day
				monthmsg = e.time.month
				yearmsg = e.time.year
				if(daymsg == '' && monthmsg == '' && yearmsg == ''){
					arr_day.push(daymsg+'_'+monthmsg+'_'+yearmsg)
					msg_per_day.push({
						'id': daymsg+'_'+monthmsg+'_'+yearmsg,
						'day': daymsg,
						'month': monthmsg,
						'year': yearmsg,
						'data': []
					})
				}
				if(!arr_day.includes(daymsg+'_'+monthmsg+'_'+yearmsg)){
					arr_day.push(daymsg+'_'+monthmsg+'_'+yearmsg)
					msg_per_day.push({
						'id': daymsg+'_'+monthmsg+'_'+yearmsg,
						'day': daymsg,
						'month': monthmsg,
						'year': yearmsg,
						'data': []
					})
				}
			})

			msg_per_day.forEach((e)=>{
				var data = []
				msg_data.forEach((f)=>{
					if(f.time.day == e.day && f.time.month == e.month && f.time.year == e.year){
						data.push(f)
					}
				})
				e.data = data
			})

			// console.log(msg_per_day)

			this.message = msg_per_day
			// console.log(this.message)
			this.scrollToBottom()
		},
		setName(){
			if(this.form.name.length > 15){
				this.form.name = this.form.name.substr(15)
			}
			if(this.form.name == ''){
				this.form.name = 'No Name'
			}
			if(this.form.name == 'Jhonarendra'){
				this.$bvModal.show('modalAdmin')
			}
			var user = {
				'name': this.form.name,
				'secret': this.form.secret
			}
			localStorage.setItem('user', JSON.stringify(user))
			this.$bvModal.hide('modalProfil')
		},
		setSecret(){
			if(this.form.secret != 'devel0per0nly'){
				this.form.is_admin = 0
				this.form.name = 'No Name'
				this.form.secret = ''
			}
			var user = {
				'name': this.form.name,
				'secret': this.form.secret
			}
			localStorage.setItem('user', JSON.stringify(user))
			this.$bvModal.hide('modalAdmin')
		},
		sendMessage(){
			if(this.form.name == 'Jhonarendra' && this.form.secret == 'devel0per0nly'){
				this.form.is_admin = 1
			} else if(this.form.name == 'Jhonarendra' && this.form.secret != 'devel0per0nly'){
				this.form.is_admin = 0
				this.form.name = 'No Name'
				this.form.secret = ''
			}
			if(this.form.message == ''){
				alert('Empty message')
				return false
			}
			if(this.disabled_send){
				alert('Please wait')
				return false
			}
			if(navigator.onLine){
				const db = firebase.firestore()
				db.collection('message')
				.add({
					name: this.form.name,
					message: this.form.message,
					is_admin: this.form.is_admin,
					is_deleted: 0,
					created_at: this.dateNow()
				})
				.then(() => {
					alert('Message sent')
					this.initialize()
					this.setCountdown()
				})
				.catch((error) => {
					console.error("Error getting documents: ", error)
				});
			} else {
				// alert('Not available in offline mode')


				alert('Message sent')
				this.initialize()
				this.setCountdown()
			}
		},
		setCountdown(){
			this.disabled_send = true
			for (var i = 0; i < this.init_send_countdown; i++) {
				setTimeout(()=>{
					this.send_countdown -= 1
					if(this.send_countdown == 0){
						this.disabled_send = false
						this.send_countdown = this.init_send_countdown
					}
				}, i*1000)
			}
		},
		scrollToBottom(){
			setTimeout(()=>{
				document.getElementById('msg-content').scroll({
					top: document.getElementById('msg-content').scrollHeight,
					behavior: 'smooth'
				})
			}, 500)
			
		}
	},
	mounted(){
		this.initialize()
	}
}

</script>

<style scoped="">
	.msg-content{
		height: 400px;
		overflow-y: auto;
	}
	.msg-bubble-wrap{
		width: 100%;
		overflow: hidden;
	}
	.msg-bubble{
		float:right;
		padding: 5px 10px;
		min-width: 20%;
		max-width: 80%;
		margin:10px 15px;
		border: 2px solid white;
	}
	.msg-bubble.admin{
		float:left;
		background: white;
		color: black;
	}
	.msg-control{
		padding: 0 15px;
	}
	.msg-control textarea{
		border-radius: 0px;
		border:2px solid white;
		background: black;
		color: white;
	}
	.msg-control button{
		background: white;
		border-radius: 0px;
	}
	.msg-control button.btn-outline{
		background: black;
		color: white;
	}
</style>