const parsingDate = {
	methods: {
		parseDate(data){
			var date = new Date(data)
			var tahun = date.getFullYear()
			var bulan = this.monthName(date.getMonth() + 1, 'en')
			var hari = date.getDate()
			
			var hari_string = date.getDate()
			if(hari_string.toString().length == 1){
				hari_string = '0'+hari_string
			}
			var jam = date.getHours()
			if(jam.toString().length == 1){
				jam = '0'+jam
			}
			var menit = date.getMinutes()
			if(menit.toString().length == 1){
				menit = '0'+menit
			}
			var detik = date.getSeconds()
			if(detik.toString().length == 1){
				detik = '0'+detik
			}

			var bulan_index = date.getMonth() + 1
			if(bulan_index.toString().length == 1){
				bulan_index = '0'+bulan_index
			}

			return {
				year: tahun,
				month: bulan,
				monthIndex: bulan_index,
				day: hari,
				dayString: hari_string,
				hour: jam,
				minute: menit,
				second: detik
			}
		},
		monthName(bulan, lang){
			if(lang == 'id'){
				switch (bulan) {
					case 1: bulan = "Januari"; break
					case 2: bulan = "Februari"; break
					case 3: bulan = "Maret"; break
					case 4: bulan = "April"; break
					case 5: bulan = "Mei"; break
					case 6: bulan = "Juni"; break
					case 7: bulan = "Juli"; break
					case 8: bulan = "Agustus"; break
					case 9: bulan = "September"; break
					case 10: bulan = "Oktober"; break
					case 11: bulan = "November"; break
					case 12: bulan = "Desember"; break
				}
				return bulan
			} else if(lang == 'en') {
				switch (bulan) {
					case 1: bulan = "January"; break
					case 2: bulan = "February"; break
					case 3: bulan = "March"; break
					case 4: bulan = "April"; break
					case 5: bulan = "May"; break
					case 6: bulan = "June"; break
					case 7: bulan = "July"; break
					case 8: bulan = "August"; break
					case 9: bulan = "September"; break
					case 10: bulan = "October"; break
					case 11: bulan = "November"; break
					case 12: bulan = "December"; break
				}
				return bulan
			}
			
		},
		dateNow(){
			var date = new Date()
			date = this.parseDate(date)
			return date.year+'-'+date.monthIndex+'-'+date.dayString+'T'+date.hour+':'+date.minute+':'+date.second
		},
		secondToMinute(s){
			var minute = ''
			var mod = 0
			if(s >= 60){
				mod = s%60
				if(mod.toString().length == 1){
					mod = '0' + mod
				}
				minute = parseInt(s/60)+':'+mod
			} else if(s < 60){
				minute = s
			} else {
				if(s.toString().length == 1){
					s = '0' + s
				}
				minute = s
			}
			return minute

		}
	}
}

export default parsingDate