<template>
	<button id="btnTotop" class="btn btn-darkblue btn-floating btn-totop circle" v-on:click="scrollTop()"></button>
</template>

<script>
	export default {
		data(){
			return {
				currentScrollTop: 0
			}
		},
		methods:{
			handleScroll(){
				var newScrollTop = window.scrollY
				if(this.currentScrollTop > newScrollTop){
					document.getElementById('btnTotop').classList.remove('slideDown')
					document.getElementById('btnTotop').classList.add('slideUp')
				} else {
					document.getElementById('btnTotop').classList.remove('slideUp')
					document.getElementById('btnTotop').classList.add('slideDown')
				}
				this.currentScrollTop = newScrollTop
			},
			scrollTop(){
				window.scroll({
					top: 0,
					behavior: 'smooth'
				})
			}
		},
		mounted(){
			window.addEventListener('scroll', this.handleScroll)
		}
	}
</script>

<style scoped="">
	.btn-totop {
		background-image:url('../assets/comp/arrow.svg');
		height: 50px;
		width: 50px;
		right: 20px;
		background-repeat: no-repeat;
		background-size: 10px;
		background-position: center;
	}

	.slideUp{
		bottom:20px;
	}
	.slideDown{
		bottom:20px;
	}

	@media screen and (max-width: 768px){
		.btn-totop{
			bottom: 80px;
		}
		.slideUp{
			animation: slideUp .5s;
			bottom:80px;
		}
		.slideDown{
			animation: slideDown .5s;
			bottom:20px;
		}
	}
	@keyframes slideUp{0%{bottom:20px}100%{bottom:80;}}
	@keyframes slideDown{0%{bottom:80px}100%{bottom:20px;}}
</style>